import React, { useState, useEffect } from "react";
import { commerce } from "./lib/commerce";

import { Products, Navbar } from "./components";
const App = () => {
  // using useState hooks to set the products state to default empty array
  const [products, setProducts] = useState([]);

  // fetching products using asynchoronous function
  const fetchProducts = async () => {
    // we have to fetch the response and wait for something
    const { data } = await commerce.products.list(); // returns promise

    // set the products
    setProducts(data);
  };

  // useEffect hooks is set to dependencies array empty so its only going to run once at the render
  useEffect(() => {
    fetchProducts(); // calls commerce product list
  }, []);

  console.log(products);

  return (
    <>
      <Navbar />
      <Products />
    </>
  );
};

export default App;
